import React from "react";
import Info from "./components/info";
import Form from "./components/form";
import Data from "./components/data";

/*const API_KEY = "74e419c8f0bdc2acdc0658376ae8d3c4";*/

class App extends React.Component {

  state = {
    country:undefined,
    capital:undefined,
    region:undefined,
    subregion:undefined,
    population:undefined,
    currencies:undefined,
    languages:undefined,
    error: undefined
  }

  gettingData = async (e) => {
    e.preventDefault();
    var country = e.target.elements.country.value;
    
    if(country) {
      const api_url = await fetch(`https://restcountries.eu/rest/v2/name/{name}?fullText=true`);
      const data = await api_url.json();

      /*var sunset = data.sys.sunset;
      var date = new Date();
      date.setTime(sunset);
      var sunset_date = date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();*/

      this.setState({
        country:data.name,
        capital:data.capital,
        region:data.region,
        subregion:data.subregion,
        population:data.population,
        currencies:data.currencies.name,
        languages:data.languages.name,
        error: undefined
      });
    } else {
      this.setState({
        country:undefined,
        capital:undefined,
        region:undefined,
        subregion:undefined,
        population:undefined,
        currencies:undefined,
        languages:undefined,
        error: "Enter the country name"
      });
      }
  }

  render() {
    return(
      <div className="wrapper">
       <div className="main">
        <div className="container">
          
            <div className="info">
              <Info />
            </div>
            <div className="form">
              <Form dataMethod={this.gettingData} />
              <Data 
                country={this.state.country}
                capital={this.state.capital}
                region={this.state.region}
                subregion={this.state.subregion}
                population={this.state.population}
                currencies={this.state.currencies}
                languages={this.state.languages}
                error={this.state.error}
              />
            </div>
          
         </div>
        </div>
      
      
      </div>
    );
  }
}

export default App;